<template>
    <div class="index">
        <!-- 二级路由 -->
        <!--  路由 keep-alive保持失活路由的状态，如果再次查看该路由时，不会重新触发生命周期钩子 -->
        <div>
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </div>
        <!-- 底部一级路由 -->
        <van-tabbar class="indexfooter" v-model="indexActive" active-color="#FF3474" :border="false">
            <van-tabbar-item v-for="(item,index) in tabbarData" :key="index"
            :name="item.name" :icon="item.icon"
            @click="togglePage({name: item.name})">{{item.title}}
            </van-tabbar-item>
        </van-tabbar>

    </div>
</template>

<script>

  import {createNamespacedHelpers} from 'vuex'
  const {mapState} = createNamespacedHelpers('indexModule');
  
    export default {
        name:'index',

        data(){
            return {
                indexActive: 'home',
            };
        },

        computed:{
            ...mapState(['tabbarData'])
        },

        methods:{
            togglePage(o){
                this.$router.push(o);
            }
        },

        created() {
            // console.log('this.$route ==> ', this.$route);

            // console.log('this.$route.name=>',this.$route.name);
            //纠正底部导航的标签
            this.indexActive = this.$route.name;

            if(this.$route.name == 'cinema' || this.$route.name == 'coming'){
                this.indexActive = 'movielist';
            }
            // 我的逻辑应该是先判断是否是cinema等，不是再按普通的要求纠正底部导航标签的，但是居然要放前面才对？？？？
        }
    }
</script>

<style lang="less" scoped>
    .index{
    padding-bottom: calc(~"1.33333rem");
    }
    
    /deep/.van-tabbar-item__icon{
         font-size: .54rem;
    }
    /deep/.van-tabbar-item {
        font-size: .4rem;
        padding-bottom: 10px;
    }
</style>